<template>
  <div class="login-view">
    <div class="card m-auto col-lg-5 col-11 text-start">
      <div class="card-body">
        <div class="logo">
          <div class="img-wrapper">
            <img :src="$baseURL + 'logo.png'" class="card-img-top">
          </div>
        </div>
        <div class="text-center mb-4">
          <h5 class="card-title">✦ TOKO SYAFIN WALLPAPER ✦</h5>
          <p class="card-text">⏴ Percantik Rumah Anda Dengan Wallpaper Murah Berkualitas ⏵</p>
        </div>
        <form @submit.prevent="Store">
          <div class="form-floating mb-3">
            <input type="email" class="form-control" id="email" placeholder="Email address" name="email" v-model="form.email" v-validate="'required|email'">
            <label for="email">Email address</label>
            <div class="invalid-feedback">
                {{ errors.first('email') }}
            </div>
          </div>
          <div class="form-floating mb-3">
            <input type="password" class="form-control" id="password" placeholder="Password" name="password" v-model="form.password" v-validate="'required'">
            <label for="password">Password</label>
            <div class="invalid-feedback">
                {{ errors.first('password') }}
            </div>
          </div>
          <button type="submit" class="btn btn-success">
            <span v-if="loading">
                <span class="spinner-border spinner-border-sm" role="status" aria-hidden="true"></span>
                Loading...
            </span>
            <span v-else>Login</span>
          </button>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data(){
      return {
        loading: false,
        form: {
          email: '',
          password: ''
        }
      }
    },
    methods: {
      Store(e) {
        e.preventDefault()
        this.$validator.validate().then(valid => {
          if (!valid) {
              console.log('not valid')
          } else {
              this.loading = true
              let formData = new FormData()
              formData.append('email', this.form.email)
              formData.append('password', this.form.password)
              this.axios.post(`auth`, formData)
                      .then((r) => {
                        if(r.data.status === 200) {
                            this.$swal({
                                position: 'center',
                                icon: 'success',
                                title: r.data.message,
                                showConfirmButton: false,
                                timer: 1500
                            })
                            this.loading = false

                            window.auth = r.data.payload

                            this.$router.push({ path: '/admin' })

                        } else {
                           this.loading = false
                            this.$swal({
                                  position: 'center',
                                  icon: 'error',
                                  title: r.data.message,
                                  showConfirmButton: false,
                                  timer: 1500
                              })
                        }
                          
                      }).catch(() => {
                          this.$swal({
                              position: 'center',
                              icon: 'error',
                              title: 'Server Error',
                              text: 'Silakan hubungi Developer',
                              showConfirmButton: false,
                          })
                      })
                }
            });
          }
    }
  }
</script>

